.l-collection {
  padding-top: 100px;
  padding-bottom: 80px;
  
  @include mq(pc){
    padding-top: 205px;
    padding-bottom: 110px;
  }

  &__heading {
    display: flex;
    justify-content: center;
    width: 100%;
    
    @include mq(pc){
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      margin-left: 30px;
    }

    @include mq(xl){
      margin-left: 0;
    }

  }

  &__bg {
    background: url("../images/common/bg-shape03.svg")no-repeat right 15% bottom 15%;
    background-size: 50%;
    padding-top: 55px;
    padding-bottom: 85px;

    @include mq(pc){
      background: url("../images/common/bg-shape03.svg")no-repeat left 15% bottom 15%;
      background-size: auto;
    }
  }


  &__slider {
    width: 100%;
    margin: 0 auto;
    position: relative;
  }

  &__more-button {
    display: flex;
    justify-content: center;
    align-items: center;
  }

}