.c-contact-button {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: $baseTextColor;
  font-family: $japanFontSanSefif;
  padding: 32px 50px;
  border: 1px solid $baseTextColor;
  letter-spacing: .1em;
  line-height: 1;

  @include mq(tb){
    width: auto;
    padding: 30px 80px;
  }
}

